window.filterSendData = function (data, type) {
  for (var key in data) {
    if (Object.prototype.hasOwnProperty.call(data, key)) {
      //过滤
      if (key === 'name') {
        console.log('data[key]');
        console.log(data[key]);
      }
      if (!data[key] && data[key] !== 0) {
        if (type === 'delete' || typeof type === 'undefined') {
          //为不显示
          delete data[key];
        } else if (type === 'blankStr') {
          //为空字符串
          data[key] = '';
        } else if (type === 'null') {
          //为null
          data[key] = null;
        }
      }
    }
  }
  return data;
};

